<template>
  <div class="container">
    <header>
      <img src="./assets/mlogo.png" alt="">
      <span class='header-title'>webook</span>
      <ul class="header-nav">
        <li><a href='/index.html'></a></li><li class="active"><a href='/newArticle.html'></a></li>
      </ul>
    </header>
    <div class="content" ref="content">
      <el-input placeholder="" v-model="contentTitle">
        <el-select v-model="app" slot="prepend" placeholder="">
          <el-option label="" value="hbpost"></el-option>
          <el-option label="" value="psbcias"></el-option>
          <el-option label="" value="predict"></el-option>
          <el-option label="" value="operation"></el-option>
          <el-option label="" value="reportmgr"></el-option>
          <el-option label="" value="surveyrpt"></el-option>
          <el-option label="" value="cval"></el-option>
        </el-select>
      </el-input>
      <div class='empty-nots' v-if="emptyNots">
        <p></p>
      </div>
      <div class='note-item flex-box animated' 
      v-bind:animate="note.animate"
      v-bind:class="note.animate" v-for="note in notes">
        <div class="note-item-pic no-grow">
          <el-tooltip class="item" effect="dark" content="" placement="right">
            <input type="file" class="fileElem" accept="image/jpeg,image/jpg,image/png,image/bmp"
              @change="handleFiles($event)"
            >
            <img v-if="note.file" v-bind:src="'http://139.129.252.72:8000/webook/400/'+note.file" v-bind:_src="note.file" alt="">
            <img v-if="!note.file" src="./assets/mining.jpg" alt="">
          </el-tooltip>
        </div>
        <div class='note-item-text' v-html="note.text" contenteditable=true v-on:click="edit"></div>
      </div>
      <div class='note-item flex-box animated' 
        v-bind:class="animate"
        v-bind:animate="animate"
        v-for="animate in animates">
        <div class="note-item-pic no-grow">
          <el-tooltip class="item" effect="dark" content="" placement="right">
            <input type="file" class="fileElem" accept="image/jpeg,image/jpg,image/png,image/bmp"
              @change="handleFiles($event)"
            >
            <img src="./assets/mining.jpg" alt="">
          </el-tooltip>
        </div>
        <div class='note-item-text' contenteditable=true v-on:click="edit">
          <p class='holder-text'></p>
        </div>
      </div>
    </div>
    <div class="aside-right">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <el-badge :value="count" class="item">
            <span style="line-height: 36px;"></span>
          </el-badge>
          <el-button style="float: right;" type="default" v-on:click="addNote" icon="plus"></el-button>
          <el-button style="float: right;margin-right:15px;" type="primary" 
            icon="check" 
            v-if="!emptyNots" 
            v-on:click="saveNote">
            
          </el-button>
        </div>
        <div class='content-layout'>
          <p class='aside-right-title'></p>
          <div class='layout-item flex-box flex-column' 
          v-bind:class="{ active: isColumn }"
          @click="changeLayout('column')">
            <div class="content-layout-pic no-grow">
              <img src="./assets/mining.jpg" alt="">
            </div>
            <div>
              ...
            </div>
          </div>  
          <div class='layout-item flex-box flex-column-reverse'
          v-bind:class="{ active: isColumnReverse }"
          @click="changeLayout('columnReverse')">
            <div class="content-layout-pic no-grow">
              <img src="./assets/mining.jpg" alt="">
            </div>
            <div>
              ...
            </div>
          </div>    
        </div>
        <div class='content-animate'>
          <p class='aside-right-title'></p>
          <el-select v-model="selectAnimate" clearable placeholder="">
            <el-option label="" value="fadeIn"></el-option>
            <el-option label="" value="rotateIn"></el-option>
            <el-option label="" value="bounceIn"></el-option>
            <el-option label="" value="flash"></el-option>
            <el-option label="" value="flip"></el-option>
            <el-option label="" value="shake"></el-option>
          </el-select>
          <el-select v-model="selectAnimateDirection" clearable placeholder="">
            <el-option label="" value="Down"></el-option>
            <el-option label="" value="Up"></el-option>
            <el-option label="" value="Left"></el-option>
            <el-option label="" value="Right"></el-option>
          </el-select>
        </div>
      </el-card>
     
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      app: '',
      count: 0,
      selectAnimate: '',
      selectAnimateDirection: '',
      contentTitle:'',
      notes: [],
      animates: [],
      emptyNots: true,
      show: true,
      postData:null,
      layout: 'column',
      isColumn: true,
      isColumnReverse: false
    }
  },
  created: function() {
    var anchor = window.location.hash.split('#');
    //console.log(anchor);
    if(anchor.length > 1){
      var key = anchor[1];
      var me = this;
      fetch('/apis/article/id/'+key).then(function(response) {
        return response.json()
      }).then(function(json) {
        me.app = json.app
        me.contentTitle = json.contentTitle;
        me.notes = json.notes;
        if(json.notes.length > 0) {
          me.emptyNots = false;
          me.count = json.notes.length;
        }
        console.log('parsed json', json)
      }).catch(function(ex) {
        me.$message.error('');
        console.log('parsing failed', ex)
      })
    }
  },
  methods: {
    changeLayout(type) {
      this.layout = type;
      if(type=='column'){
        this.isColumn = true;
        this.isColumnReverse = false;
      }else{
        this.isColumn = false;
        this.isColumnReverse = true;
      }
    },
    addNote() {
      var suffix = '';
      if(this.selectAnimate == 'fadeIn' || this.selectAnimate == 'bounceIn'){
        suffix = this.selectAnimateDirection + ' ';
      }
      var animate = this.selectAnimate + suffix + 'flex-' + this.layout;
      this.animates.push(animate);
      this.emptyNots = false;
      this.count++;
    },
    edit() {
      this.show = false;
    },
    saveNote() {
      if(this.select == ''){
        this.$message.error('');
      }else if(this.contentTitle == ''){
        this.$message.error('');
      }else{
        var els = this.$refs.content.querySelectorAll('div.note-item');
        this.notes = [];
        for(var i = 0; i<els.length; i++) {
          var item = {};
          item.animate = els[i].getAttribute('animate');
          //item.animate = 'flex-column-reverse';
          item.text = els[i].querySelector('.note-item-text').innerHTML;
          item.file = els[i].querySelector('img').getAttribute('_src');
          if(item.text!='<p class="holder-text"><br></p>'){
            this.notes.push(item);
          }
        }
        var article = {
          app: this.app,
          contentTitle: this.contentTitle,
          notes: this.notes
        }
        var anchor = window.location.hash.split('#');
        if(anchor.length > 1) {
          article.id = anchor[1];
        }
        var me = this;
        fetch('/apis/create', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(article)
        }).then(function(response) {
          return response.json()
        }).then(function(json) {
          me.$notify({
            title: '',
            message: '',
            type: 'success'
          });
          Object.assign(me.$data, me.$options.data())
          //console.log('parsed json', json)
        }).catch(function(ex) {
          me.$message.error('');
          console.log('parsing failed', ex)
        })
      }
    },
    handleFiles(event) {
      var file = event.currentTarget.files[0];
      var img = event.currentTarget.nextElementSibling;
      img.src = window.URL.createObjectURL(file);
      img.onload = function() {
        window.URL.revokeObjectURL(this.src);
      }
      var me = this;
      var data = new FormData();
      data.append('file', file)
      //data.append('user', 'hubot')
      fetch('/apis/upload', {
        method: 'POST',
        body: data
      }).then(function(response) {
        return response.json()
      }).then(function(json) {
        img.setAttribute('_src',json[0].name);
        //console.log('parsed json', json)
      }).catch(function(ex) {
        me.$message.error('');
        console.log('parsing failed', ex)
      })
    }
  }
}
</script>

<style>
body {
  font-family: Helvetica, sans-serif;
}
</style>



